import { default as React, useEffect, useRef, useCallback } from 'react';
import { useState } from 'react';
import { Typer } from 'react-typetool';
import { useTranslation } from 'react-i18next';
import classnames from 'classnames';

import { HorizontalScroll, ShowcaseGroup } from '../components';
import * as Utils from '../utilities';
import { IntroUtils } from '../utilities';
import { useWindow } from '../services';

const HomePage = () => {
	const [shouldBlink, setShouldBlink] = useState(true);
	const [shouldShowCaret, setShouldShowCaret] = useState(true);
	const { windowSize } = useWindow();
	const { t } = useTranslation();
	const [showIntroParagraph, setShowIntroParagraph] = useState(false);
	let typerCaretRef = useRef(null);
	let scrollTrackRef = useRef(null);
	let scrollTrackFillRef = useRef(null);
	let scrollContainerRef = useRef(null);
	const [introUtils, setIntroUtils] = useState();

	useEffect(() => {
		setIntroUtils(new IntroUtils());
	}, []);

	useEffect(() => {
		if (!typerCaretRef || !typerCaretRef.current) return;
		if (!introUtils) return;
		introUtils.setCaret(typerCaretRef.current);
		introUtils.setStopTyperCaretCB(() => {
			setShouldBlink(false);
			setShouldShowCaret(true);
			setShowIntroParagraph(true);
		});
	}, [typerCaretRef, introUtils]);

	const onFinishTyping = useCallback(() => {
		if (!introUtils) return;
		introUtils.onFinishTyping();
	}, [introUtils]);

	const setScrollDirection = useCallback(() => {
		if (!windowSize) return;
		if (windowSize.width < 1360) {
			if (scrollContainerRef && scrollContainerRef.current) {
				// console.log(scrollContainerRef.current.clientHeight);
				const height = scrollContainerRef.current.clientHeight;
				scrollTrackRef.current.style.height = `calc(${height}px - 140vh)`;
				scrollTrackRef.current.style.width = '10px';
				scrollTrackFillRef.current.style.width = `100%`;
			}
		} else {
			scrollTrackRef.current.style.width =
				'calc(100% - (100vw - 150px) + 50vw)';
			scrollTrackRef.current.style.height = '10px';
			scrollTrackFillRef.current.style.height = `100%`;
		}
	}, [windowSize, scrollContainerRef]);

	const updateScrollTrackFill = (scrollData) => {
		if (!windowSize) return;
		const caret = typerCaretRef.current;
		if (windowSize.width < 1360) {
			// console.log(scrollData.position, scrollData.min, scrollData.max);
			const trackHeight = scrollTrackRef.current.offsetHeight;
			const trackPosMiddle = windowSize.height / 2 - 150;
			const customPosition =
				scrollData.position - trackPosMiddle + caret.offsetHeight / 2;
			const customPositionMax = scrollData.max - windowSize.height / 2 + 150;
			// customPosition = Utils.clamp(customPosition, 0, scrollData.max)
			let fillHeight = Utils.map(
				customPosition,
				scrollData.min,
				customPositionMax,
				0,
				trackHeight
			);
			// console.log(customPosition, customPositionMax, scrollData.position, scrollData.max);
			fillHeight -= caret.offsetHeight;
			fillHeight = Utils.clamp(fillHeight, 0, trackHeight);
			scrollTrackFillRef.current.style.height = `${fillHeight}px`;
			console.log(fillHeight, trackHeight);
			// console.log(customPosition, scrollData.max);
		} else {
			const trackWidth = scrollTrackRef.current.offsetWidth;
			const trackPos = windowSize.width / 2 - 150;
			const customPosition =
				scrollData.position - trackPos + caret.offsetWidth / 2;
			let fillWidth = Utils.map(
				customPosition,
				scrollData.min,
				scrollData.max - windowSize.width / 2 + 150,
				0,
				trackWidth
			);
			fillWidth -= caret.offsetWidth / 2;
			fillWidth = Utils.clamp(fillWidth, 0, trackWidth);
			scrollTrackFillRef.current.style.width = `${fillWidth}px`;
		}
	};

	useEffect(setScrollDirection, [windowSize]);

	const onWheel = (scrollData) => {
		if (!introUtils) return;

		introUtils.onWheel(scrollData);
		updateScrollTrackFill(scrollData);
	};

	return (
		<div className="page homePage">
			<HorizontalScroll
				onWheel={onWheel}
				ref={scrollContainerRef}
				onLoad={setScrollDirection}
			>
				<div className="homePage__introContainer">
					<div className="homePage__introText">
						<Typer
							text={t('content:introTyper')}
							classes="introTyper"
							blink={shouldBlink}
							onFinish={onFinishTyping}
							showCaret={shouldShowCaret}
							getCaretRef={(ref) => (typerCaretRef = ref)}
							useDefaultStyle={false}
						/>
						<p
							className={classnames(
								'homePage__introParagraph',
								!showIntroParagraph && 'homePage__introParagraph--hide'
							)}
							dangerouslySetInnerHTML={{ __html: t('content:introParagraph') }}
						></p>
					</div>
				</div>
				<ShowcaseGroup />
				<div className="homePage__outroContainer"></div>
				<div className="scrollTrack" ref={scrollTrackRef}>
					<div className="scrollTrack__fill" ref={scrollTrackFillRef}></div>
				</div>
			</HorizontalScroll>
		</div>
	);
};

export default HomePage;
