import { default as React, useContext, createContext, useEffect, useState } from 'react';
import { useStorage } from './storage.service';

const WindowContext = createContext();
const useWindow = () => useContext(WindowContext);

const WindowProvider = ({ children }) => {
	const [windowSize, setWindowSize] = useState();

	const updateWindowSize = () => {
		setWindowSize({
			height: window.innerHeight,
			width: window.innerWidth,
		});
	};

	useEffect(() => {
		updateWindowSize();
		
		const resizeListener = window.addEventListener('resize', () => {
			updateWindowSize();
		});

		return () => window.removeEventListener('resize', resizeListener);
	}, []);

	const exports = {
		windowSize,
	};

	return (
		<WindowContext.Provider value={exports}>{children}</WindowContext.Provider>
	);
};

export { WindowContext, WindowProvider, useWindow };
