import React, { useState } from 'react';
import classnames from 'classnames';
import {
	FaTimes,
	FaCaretLeft,
	FaCaretRight,
} from 'react-icons/fa';

export const MediaList = ({ mediaList, className }) => {
	const [openedMedia, setOpenedMedia] = useState();

	const openPrev = () => {
		const prevIndex = mediaList.indexOf(openedMedia) - 1;
		if(prevIndex < 0) return;
		setOpenedMedia(mediaList[prevIndex]);
	};

	const openNext = () => {
		const nextIndex = mediaList.indexOf(openedMedia) + 1;
		if(nextIndex >= mediaList.length) return;
		setOpenedMedia(mediaList[nextIndex]);
	};

	return (
		<div className={classnames('mediaList', className)}>
			{Array.isArray(mediaList) &&
				mediaList.map(
					(media) =>
						media.src && (
							<div
								key={media.src}
								className="mediaList__element"
								onClick={() => setOpenedMedia(media)}
							>
								<img src={media.src} alt={media.title} />
								<div className="mediaList__elementTitle">{media.title}</div>
							</div>
						)
				)}
			{openedMedia && openedMedia.src && (
				<div className="mediaList__display">
					<div className="mediaList__displayContent">
						<FaTimes
							className="mediaList__icon mediaList__icon--close"
							onClick={() => setOpenedMedia(null)}
						/>
						<div className="mediaList__displayTitle">{openedMedia.title}</div>
						<img src={openedMedia.src} alt={openedMedia.title} />
						<div className="mediaNav">
							<FaCaretLeft className="mediaNav__arrow mediaNav__arrow--prev" onClick={openPrev} />
							{Array.isArray(mediaList) &&
								mediaList.map((media) => (
									<div
										key={`${media.src}-display`}
										className={classnames(
											'mediaNav__link',
											media.src === openedMedia.src &&
												'mediaNav__link--selected'
										)}
										onClick={() => setOpenedMedia(media)}
									></div>
								))}
							<FaCaretRight className="mediaNav__arrow mediaNav__arrow--next" onClick={openNext} />
						</div>
					</div>
				</div>
			)}
		</div>
	);
};
