import React, { useState, useEffect, useRef, useCallback } from 'react';
import classnames from 'classnames';
import * as Utils from '../../utilities';
import { useWindow } from '../../services';

export const HorizontalScroll = React.forwardRef(
	({ children, classes, scrollSpeed = 1, onWheel, onLoad }, ref) => {
		const contentRef = useRef(null);
		// const ref = useRef(null);
		const [contentPosLeft, setContentPosLeft] = useState(0);
		const [contentStyle, setContentStyle] = useState({
			position: 'absolute',
		});
		const [max, setMax] = useState(1000);
		const { windowSize } = useWindow();

		const handleVerticalScroll = useCallback(
			(event) => {
				// onWheel({
				// 	position: window.scrollY,
				// 	min: 0,
				// 	max,
				// });
				setContentPosLeft(window.scrollY);
				// console.log(window.scrollY, max);
			},
			[onWheel]
		);

		useEffect(() => {
			// if (!rootRef || !rootRef.current) return;
			if (!window) return;
			// console.log(rootRef.current);
			const eventListener = window.addEventListener(
				'scroll',
				handleVerticalScroll
			);

			return () => window.removeEventListener('scroll', eventListener);
		}, [window]);

		const setHeights = useCallback(() => {
			if (!windowSize) return;

			ref.current.style.height = `${
				contentRef.current.getBoundingClientRect().height + windowSize.height
			}px`;
		}, [contentRef, ref, windowSize]);

		const setWidths = useCallback(() => {
			if (!windowSize) return;
			if (windowSize.width < 1360) {
				setContentStyle((prev) => ({
					...prev,
					left: 0,
				}));

				setHeights();
				ref.current.style.height = `${
					contentRef.current.getBoundingClientRect().height
				}px`;
				setMax(ref.current.getBoundingClientRect().height - windowSize.height);
				return;
			}
			ref.current.style.width = `${
				contentRef.current.getBoundingClientRect().width
			}px`;
			setMax(ref.current.getBoundingClientRect().width);
		}, [contentRef, ref, windowSize, setHeights]);

		useEffect(() => {
			if (!windowSize) return;

			if (onWheel)
				onWheel({
					position: contentPosLeft,
					min: 0,
					max,
				});

			if (windowSize.width < 1360) return;

			setContentStyle((prev) => ({
				...prev,
				left: -contentPosLeft,
			}));
		}, [contentPosLeft, max, onWheel]);

		useEffect(() => {
			setWidths();
		}, [contentRef, ref, setWidths]);

		const onScroll = useCallback(
			(event = { deltaY: 0 }) => {
				if (!windowSize) return;
				if (windowSize.width < 1360) {
					// console.log(window.scrollY);
				} else {
					const deltaY = scrollSpeed * event.deltaY;
					setContentPosLeft((prev) => Utils.clamp(prev + deltaY, 0, max));
				}
			},
			[max, scrollSpeed, windowSize]
		);

		// const onDrag = useCallback(
		// 	(event) => {
		// 		// if (!windowSize) return;
		// 		// const deltaY = scrollSpeed * event.deltaY;
		// 		// setContentPosLeft((prev) => Utils.clamp(prev + deltaY, 0, max));
		// 		// console.log(event.touches[0].clientY);
		// 		console.log(event);
		// 		console.log(event);
		// 		// setDragStart();
		// 	},
		// 	[max, scrollSpeed, windowSize]
		// );

		useEffect(() => {
			onScroll();
		}, [max, onScroll]);

		const onContentLoad = () => {
			setWidths();
			if (onLoad) onLoad();
		};

		return (
			<div
				className={classnames('horizontalScroll', classes)}
				onWheel={onScroll}
				// onScroll={onDrag}
				ref={ref}
			>
				<div
					className="horizontalScroll__content"
					ref={contentRef}
					style={contentStyle}
					onLoad={onContentLoad}
				>
					{children}
				</div>
			</div>
		);
	}
);
