import * as Utils from './Utils';

export class IntroUtils {
	constructor() {
		this.caretAnimationDone = false;
		this.hasScrolled = false;
		// this.caret = null;
		this.finishedTyping = false;
		this.stopTyperCaretCB = null;
		this.windowSize = { width: window.innerWidth, height: window.innerHeight };

		window.addEventListener('resize', () => {
			this.windowSize = {
				width: window.innerWidth,
				height: window.innerHeight,
			};
			if (this.onWindowSizeChange) this.onWindowSizeChange();
		});
	}

	setCaret(caretRef) {
		this.caret = caretRef;
	}

	// setWindowSize(windowSize) {
	// 	console.log(windowSize);
	// 	this.windowSize = windowSize;
	// }

	setStopTyperCaretCB(cb) {
		this.stopTyperCaretCB = cb;
	}

	initializeCaretPosition() {
		let positionTimeout;
		console.log('initializeCaretPosition');

		if (!this.windowSize) return;
		if (this.windowSize.width < 1360) {
			positionTimeout = setTimeout(() => {
				this.caret.style.right = `${this.windowSize.width / 2 - 50}px`;
				this.caret.style.width = '100px';
			}, 10);
		} else {
			positionTimeout = setTimeout(() => {
				this.caret.style.right = '100px';
				this.caret.style.width = '100px';
			}, 10);
		}

		this.updateCaretPosition();

		const waitForFinishTimeout = setTimeout(() => {
			this.caretAnimationDone = true;
		}, 1000);

		// return () => {
		// 	clearTimeout(positionTimeout);
		// 	clearTimeout(waitForFinishTimeout);
		// };
	}

	updateCaretPosition(scrollData = null) {
		if (!this.windowSize) return;
		if (!this.finishedTyping) return;
		console.log('updateCaretPosition');
		const halfScreen = this.windowSize.height / 2;

		if (this.windowSize.width < 1360) {
			this.caret.style.display = 'none';
			const caretPosRight =
				this.windowSize.width / 2 - this.caret.offsetWidth / 2;
			this.caret.style.right = `${caretPosRight}px`;
			this.caret.style.position = 'fixed';
			// if (scrollData.position > (halfScreen + (this.caret.offsetWidth / 2))) {
			// } else {
			// 	this.caret.style.position = 'absolute';
			// }
		} else {
			this.caret.style.display = 'initial';
			this.caret.style.position = 'fixed';
			const positionTop =
				this.windowSize.height / 2 - this.caret.offsetHeight / 2;
			this.caret.style.top = `${positionTop}px`;
		}

		if (!scrollData) return;

		if (this.windowSize.width < 1360) {
			const caretPosMin = this.windowSize.height / 2 - this.caret.offsetHeight / 2;
			const caretPosMax = this.windowSize.height - 150 - (this.caret.offsetHeight / 2);

			const fixedScrollPosition = scrollData.position + caretPosMin;

			let caretPos = Utils.clamp(
				fixedScrollPosition,
				caretPosMin,
				caretPosMax
			);

			caretPos = Utils.map(caretPos, caretPosMin, caretPosMax, caretPosMax, caretPosMin);

			// this.caret.style.top = `calc(100vh - 100px - ${
			// 	this.caret.offsetHeight / 2
			// }px)`;

			this.caret.style.top = `${caretPos}px`;
			// console.log(caretPos);

			// if (this.caretAnimationDone || this.hasScrolled) {
			// 	this.caret.style.transition = 'top 200ms ease-out';
			// 	// this.caret.style.top = `${caretPosTop}px`;
			// }
		} else {
			const maxCaretPosRight =
				this.windowSize.width / 2 - this.caret.offsetWidth / 2;
			// console.log(scrollData);
			const caretPosRight = Utils.clamp(
				scrollData.position + 100,
				100,
				maxCaretPosRight
			);
			if (this.caretAnimationDone || this.hasScrolled) {
				this.caret.style.transition = 'right 200ms ease-out';
				this.caret.style.right = `${caretPosRight}px`;
			}
		}
	}

	stopTyperCaret() {
		console.log('stopTyperCaret');
		if (this.stopTyperCaretCB) this.stopTyperCaretCB();
		if (!this.windowSize) return;
		this.caret.style.width = '100px';
		this.caret.style.height = '100px';
		const currentCaretPosition = this.caret.getBoundingClientRect();
		const positionRight =
			this.windowSize.width - currentCaretPosition.left - 9.6;
		this.caret.style.right = `${positionRight}px`;
		this.caret.style.top = `${currentCaretPosition.top}px`;
		// console.log(currentCaretPosition.top);
		this.initializeCaretPosition();
	}

	onFinishTyping() {
		console.log('finishedTyping');
		if (this.finishedTyping) return;
		if (!this.caret) return;
		this.stopTyperCaret();
		this.finishedTyping = true;
	}

	onWindowSizeChange() {
		this.updateCaretPosition();
	}

	onWheel(scrollData) {
		if (!this.caret) return;
		this.hasScrolled = scrollData.position > 0;

		if (this.hasScrolled && !this.finishedTyping)
			this.onFinishTyping();

		this.updateCaretPosition(scrollData);
		// if (this.windowSize.width < 1360) {
		// 	const minCaretPosTop =
		// 		this.windowSize.height - 100 - this.caret.offsetHeight / 2;
		// 	const maxCaretPosTop =
		// 		this.windowSize.height / 2 - this.caret.offsetHeight / 2;
		// 	// console.log(scrollData.position);
		// 	// const caretPosTop = Utils.clamp(
		// 	// 	minCaretPosTop - scrollData.position,
		// 	// 	maxCaretPosTop,
		// 	// 	minCaretPosTop,
		// 	// );
		// 	// console.log(minCaretPosTop, maxCaretPosTop, caretPosTop);
		// 	if (this.caretAnimationDone || this.hasScrolled) {
		// 		this.caret.style.transition = 'top 200ms ease-out';
		// 		// this.caret.style.top = `${caretPosTop}px`;
		// 	}
		// } else {
		// 	const maxCaretPosRight =
		// 		this.windowSize.width / 2 - this.caret.offsetWidth / 2;
		// 	// console.log(scrollData.position);
		// 	const caretPosRight = Utils.clamp(
		// 		scrollData.position + 100,
		// 		100,
		// 		maxCaretPosRight
		// 	);
		// 	if (this.caretAnimationDone || this.hasScrolled) {
		// 		this.caret.style.transition = 'right 200ms ease-out';
		// 		this.caret.style.right = `${caretPosRight}px`;
		// 	}
		// }
	}
}
