import * as firebase from 'firebase/app';
import 'firebase/storage';
import { firebaseConfig } from '../config';

export class FirestoreStorage {
	constructor() {
		if (!firebase.apps.length) {
			firebase.initializeApp(firebaseConfig);
		}

		this.firestoreStorage = firebase.storage();
	}

	async getCollection(prefix, collection) {
		const fetchedCollectionSnapshot = await this.firestoreStorage
			.ref(`${prefix}/${collection}`)
			.listAll();

		const newItems = await Promise.all(fetchedCollectionSnapshot.items.map(async (ref) => ({
			...(await ref.getMetadata()),
			src: await ref.getDownloadURL(),
		})));
		return newItems;
	}
}
