import {
	default as React,
	useContext,
	createContext,
	useEffect,
	useState,
} from 'react';

import { Firestore, FirestoreStorage } from '../storage';

const StorageContext = createContext();
const useStorage = () => useContext(StorageContext);

const StorageProvider = ({ children }) => {
	const [firestore, setFirestore] = useState();
	const [firebaseStorage, setFirebaseStorage] = useState();
	const [isStorageLoaded, setIsStorageLoaded] = useState(false);

	const [allWork, setAllWork] = useState([]);

	const [allWorkMedia, setAllWorkMedia] = useState({});

	const initializeFirebase = async () => {
		const db = new Firestore();
		const files = new FirestoreStorage();
		setFirestore(db);
		setFirebaseStorage(files);
	};

	const initializeWork = async () => {
		const fetchedWork = await firestore.getCollection('work');
		setAllWork(fetchedWork);
	};

	useEffect(() => {
		initializeFirebase();
	}, []);

	useEffect(() => {
		if (!firestore) return;
		initializeWork();
	}, [firestore]);

	useEffect(() => {
		if (!!firestore && !!firebaseStorage && allWork.length > 0) setIsStorageLoaded(true);
	}, [firestore, firebaseStorage, allWork]);

	const getAllWork = () => {
		return allWork;
	};

	const getWorkById = (id) => {
		return allWork.find((work) => work.id === id);
	};

	const fetchMediaById = async (id) => {
		return new Promise(async (resolve, reject) => {
			if (!firebaseStorage) return reject('Firebase Storage not yet loaded.');

			const fetchedWorkFiles = await firebaseStorage.getCollection('work', id);

			const newMedia = {
				media: fetchedWorkFiles,
			};

			setAllWorkMedia((prev) => ({
				...prev,
				[id]: newMedia,
			}));

			return resolve(newMedia);
		});
	};

	const getWorkMediaById = async (id) => {
		return new Promise(async (resolve, reject) => {
			if (allWorkMedia && allWorkMedia[id]) {
				return resolve(allWorkMedia[id]);
			}

			try {
				const newMedia = await fetchMediaById(id);
				return resolve(newMedia);
			} catch (error) {
				console.log(error);
				return reject(error);
			}
		});
	};

	const exports = {
		getAllWork,
		getWorkById,
		getWorkMediaById,
		isStorageLoaded,
	};

	return (
		<StorageContext.Provider value={exports}>
			{children}
		</StorageContext.Provider>
	);
};

export { StorageContext, StorageProvider, useStorage };
